import { useDrop } from 'react-dnd'
import React from 'react';
import ItemType from './ItemType';
import ImgBox from './ImgBox'
import InputBox from './InputBox'
// const house = require('./images/house.jpg')
import picture from './images/pic4.jpg'
function ImgBoxCon(props) {
    const [{isOver},drop] = useDrop({
      accept:[ItemType.Box,ItemType.ImgBox,ItemType.InputBox,ItemType.Input],
      drop(item,monitor){
          
        console.log('drop',monitor.getClientOffset(),monitor.getSourceClientOffset())
        if(item.type==='Box'){
            props.adddropedImg(
                Object.assign({},item.info,monitor.getClientOffset())
            )
            console.log('Box',monitor.getClientOffset(),monitor.getSourceClientOffset())
        }
        if(item.type==='ImgBox'){
            props.updatedropedImg(
                Object.assign({},item.info,monitor.getClientOffset())
            )
            console.log('ImgBox',monitor.getClientOffset(),monitor.getSourceClientOffset())
        }
        if(item.type==='Input'){
            props.adddropedInput(
                Object.assign({},item.info,monitor.getClientOffset())
            )
        }
        if(item.type==='InputBox'){
            props.updatedropedInput(
                Object.assign({},item.info,monitor.getClientOffset())
            )
        }
      },
      collect : monitor =>({
        isOver: monitor.isOver(),
      }),
    })
    return (
    <div 
    ref={drop}
    id='imgBoxCon'
    style={{
    width:'100%',
    height:'100%',
    position:'relative',
    // backgroundColor:isOver?'#fff':'#ccc',
    background: `url(${picture}) no-repeat center`,
    backgroundSize:'100%',
}}>
    {props.imgList?props.imgList.map((item,index)=>{return <ImgBox key={index} info={item}></ImgBox>}):null}
    {
        props.textList?props.textList.map((item,index)=>{return <InputBox key={index} info={item}></InputBox>}):null
    }
</div>)
  }
  export default ImgBoxCon


