import React from 'react';
import { DragPreviewImage, useDrag } from 'react-dnd';
import ItemType from './ItemType';
function ImgBox(props) {
	const [{ isDragging }, drag, preview] = useDrag({
		item: { type: ItemType.ImgBox, ...props },
		collect: monitor => ({
			isDragging: monitor.isDragging(),
		}),
	});
	const opacity = isDragging ? 0.4 : 1;
	return (
		<i>
			{/* <DragPreviewImage style={{width:30,height:30}} connect={preview} src={require(props.info.defaultUrl + '')} /> */}
			{/*  */}
			<div
				className='img-gg'
				ref={drag}
				style={{
					width: props.info.width,
					height:props.info.height,
					borderRadius:10,
					position:'absolute',
					top:props.info.top,
					left:props.info.left,
					// opacity,
					background:isDragging?'#fff':'',
                    cursor: 'move',
				}}
			>
				<img src={require(props.info.defaultUrl + '')} alt="" style={{opacity, width: props.info.width,height:props.info.height}} />
			</div>
		</i>
	);
};
export default ImgBox;



